<template>
  <div class="practices">
    <!-- search input -->
    <div class="custom-search d-flex justify-content-between align-items-center">
      <b-form-group>
        <div class="form-group-item">
          <label class="mr-1">Search</label>
          <b-form-input
            v-model="searchTerm"
            placeholder="Search"
            type="text"
            class="d-inline-block"
          />
        </div>
      </b-form-group>

      <b-form-group>
        <div class="form-group-item">
          <label class="mr-1 text-nowrap">EHR System</label>
          <b-form-select
            v-model="selected"
            label="title"
            :options="filterOptions.filterDropdownItems"
          />
        </div>
      </b-form-group>

      <b-form-group>
        <div class="form-group-item">
          <label class="mr-1 text-nowrap">Assigned User</label>
          <b-form-select
            v-model="selected"
            label="title"
            :options="filterOptions.filterDropdownItems"
          />
        </div>
      </b-form-group>

      <b-form-group>
        <div class="form-group-item">
          <label class="mr-1">Status</label>
          <b-form-select
            v-model="selected"
            label="title"
            :options="filterOptions.filterDropdownItems"
          />
        </div>
      </b-form-group>

      <b-button
        v-ripple.400="'rgba(0, 207, 232, 0.15)'"
        variant="info"
        pill
      >
        <feather-icon
          icon="PlusIcon"
          class="mr-50"
        />
        <span class="align-middle">Add practice</span>
      </b-button>

    </div>

    <!-- table -->
  
    <vue-good-table
      :columns="columns"
      :rows="rows"
      :rtl="direction"
      :search-options="{
        enabled: true,
        externalQuery: searchTerm }"
  
      :pagination-options="{
        enabled: true,
        perPage:pageLength
      }"
    >
      <template
        slot="table-row"
        slot-scope="props"
      >
        <div>
          <!-- Column: practice -->
          <span
            v-if="props.column.field === 'practice'"
          >
            <span class="text-practice">{{ props.row.practice }}</span>
            <br>
            <span class="text-id">{{ props.row.id }}</span>
          </span>

          <!-- Column: Address -->
          <span v-else-if="props.column.field === 'address'">
            <span>{{ props.row.address }}</span>
          </span>

          <!-- Column: Contact -->
          <span v-else-if="props.column.field === 'contact'">
            <span>{{ props.row.contact }}</span>
          </span>

          <!-- Column: Status -->
          <span v-else-if="props.column.field === 'status'">
            <b-badge :variant="statusVariant(props.row.status)">
              {{ props.row.status }}
            </b-badge>
          </span>

          <!-- Column: Actions -->
          <span v-else class="action-col">
              <feather-icon
                  icon="EyeIcon"
                  class="mr-50"
                />
              <span>{{ props.row.actions }}</span>
          </span>
        </div>
      </template>

      <!-- pagination -->
      <template
        slot="pagination-bottom"
        slot-scope="props"
      >
        <div class="pagination-bottom d-flex justify-content-between flex-wrap pt-1">
          <div class="d-flex align-items-center mb-0 text-muted">
            <span class="text-nowrap ">
              Showing 1 
            </span>
            <span class="prefix"> of </span>
            <span class="text-nowrap"> {{ props.total }} entries </span>
          </div>

          <div class="d-flex">
            <div class="d-flex align-items-center">
              <span>Show</span>
              <b-form-select
                v-model="pageLength"
                :options="['3','5','10']"
                class="mx-1"
                @input="(value)=>props.perPageChanged({currentPerPage:value})"
              />
            </div>

            <b-pagination
              :value="1"
              :total-rows="props.total"
              :per-page="pageLength"
              first-number
              last-number
              align="right"
              prev-class="prev-item"
              next-class="next-item"
              class="mb-0"
              @input="(value)=>props.pageChanged({currentPage:value})"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>
          </div>
        </div>
      </template>
    </vue-good-table>
  </div>
</template>

<script>
import 'vue-good-table/dist/vue-good-table.css'
import {
  BAvatar, BBadge, BPagination, BFormGroup, BFormInput, BFormSelect, BDropdown, BDropdownItem,
} from 'bootstrap-vue'
import { VueGoodTable } from 'vue-good-table'
import store from '@/store/index'

import { BButton } from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'

export default {
  components: {
    VueGoodTable,
    BAvatar,
    BBadge,
    BPagination,
    BFormGroup,
    BFormInput,
    BFormSelect,
    BDropdown,
    BDropdownItem,
    BButton,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      pageLength: 5,
      dir: false,
      columns: [
          {
          label: 'PRACTICE',
          field: 'practice',
           filterOptions: {
            styleClass: 'class1', // class to be added to the parent th element
              enabled: true, // enable filter for this column
              filterValue: null, // initial populated value for this filter
              trigger: 'enter', //only trigger on enter not on keyup 
              filterFn: this.columnFilterFn,
            },
        },
        {
          label: 'ADDRESS',
          field: 'address',
        },
        {
          label: 'CONTACT INFO',
          field: 'contact',
        },
        {
          label: 'STATUS',
          field: 'status',
          filterOptions: {
            styleClass: 'class1', // class to be added to the parent th element
            enabled: true, // enable filter for this column
            filterValue: null, // initial populated value for this filter
            trigger: 'enter', //only trigger on enter not on keyup 
            filterFn: this.columnFilterFn, //custom filter function that
            filterDropdownItems: ["active", "inactive"],
          },
        },
        {
          label: 'ACTIONS',
          field: 'actions',
        },
        
      ],
      filterOptions: {
        filterDropdownItems: ["Active","Inactive"], // dropdown (with selected values) instead of text input
      },
      rows: [
        { 
          id:'ID: 94723',
          practice:"Family & Wellness Medicine, LLC",
          address: '34004 16th Ave. S., Ste. 100 Federal Way, WA 98003', 
          contact: `wsomerlie1l@accuweather.com 
                    Phone: 253-944-1223 
                    Fax: 253-944-1255`,
          status: "Active",
          actions: 'View' 
        },
        { 
          id:'ID: 35471',
          practice:"Focus Health DPC",
          address: '2522 N. Proctor St., #228 Tacoma, WA 98406', 
          contact: 'Phone: 206-372-3194',
          status: "Active",
          actions: 'View' 
        },
        { 
          id:'ID: 98756',
          practice:"The Manette Clinic",
          address: '1100 Wheaton Way, Ste. F and G Bremerton, WA 98310', 
          contact: 'wsomerlie1l@accuweather.com Phone:  253-944-1223 Fax: 253-944-1255',
          status: "Active",
          actions: 'View' 
        },
        { 
          id:'ID: 65487',
          practice:"Nurture Well Center",
          address: '34004 16th Ave. S., Ste. 100 Federal Way, WA 98003', 
          contact: 'wsomerlie1l@accuweather.com Phone:  253-944-1223 Fax: 253-944-1255',
          status: "Inactive",
          actions: 'View' 
        },
        { 
          id:'ID: 48971',
          practice:"Family & Wellness Medicine, LLC",
          address: '34004 16th Ave. S., Ste. 100 Federal Way, WA 98003', 
          contact: 'wsomerlie1l@accuweather.com Phone:  253-944-1223 Fax: 253-944-1255',
          status: "Inactive",
          actions: 'View' 
        },
      ],
      searchTerm: '',
      selected:null
    }
  },
  computed: {
    statusVariant() {
      const statusColor = {
        /* eslint-disable key-spacing */
        Active       : 'light-success',
        Inactive     : 'light-dark',
        /* eslint-enable key-spacing */
      }

      return status => statusColor[status]
    },
    direction() {
      if (store.state.appConfig.isRTL) {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.dir = true
        return this.dir
      }
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.dir = false
      return this.dir
    },
  },
  watch:{
    selected(){
        let found = this.columns.find((c) => {
          return c.field == 'status'
        });
        found.filterOptions.filterValue = this.selected;
    }
  },
  created() {
    // this.$http.get('/good-table/basic')
    //   .then(res => { this.rows = res.data })
  },
}
</script>

<style>
  .header-navbar.navbar-shadow {
    box-shadow: 0 4px 24px 0 rgb(34 41 47 / 25%);
  }
  .custom-search {
    background-color: #fff;
    padding: 17px 24px;
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    flex-wrap: wrap;
    
  } 
  .custom-search .form-group {
    margin-bottom: 0;
    width: 100%;
    max-width: 20%;
  }
  .custom-search .form-group-item {
    display: flex;
    align-items: center;
  }

  .custom-search .custom-select,
  .custom-search .form-control {
    max-height: 32px;
  }
  .custom-search .btn {
    font-size: 14px;
    max-height: 28px;
    line-height: 1;
    display: flex;
    align-items: center;
  }
  .practices table.vgt-table {
    color: #645E7A;
    font-size: 14px;
    line-height: 1.4;
    border: none;
  }
  .practices .vgt-table thead th {
    /* background: #E5E5E5; */
    border: none;
  }
  .vgt-table.bordered td,
  .vgt-table.bordered th {
    border-left: none;
    border-right: none;
    padding: 15px 24px;
    vertical-align: middle;
  }
  .vgt-table.bordered thead tr:nth-child(2){
    display: none;
  }
  .text-id {
    font-size: 12px;
    color: rgba(100, 94, 122, 0.6);
  }
  .action-col {
    cursor: pointer;
    color: #42BFFF;
    font-weight: 500;
  }
  .vgt-table th.sortable:last-child:before,
  .vgt-table th.sortable:last-child:after {
    display: none;
  }
  .content-body {
    background-color: #fff;
    box-shadow: 0 4px 24px 0 rgb(34 41 47 / 10%);
    border-radius: 0.5rem;
  }
  .pagination-bottom {
    background-color: #fff;
    padding: 0 24px 15px;
    margin-top: 30px;
    border-top: 1px solid #dcdfe6;
    border-bottom-left-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
  }
  .pagination-bottom .prefix {
    padding: 0 5px;
  }
  .pagination-bottom .custom-select {
    font-size: 10px;
    line-height: 1;
    max-height: 29px;
  }
  .page-item.active .page-link {
    background-color: #42BFFF;
  }

  @media (max-width: 991px) {
    .custom-search {
      padding: 17px 10px;
    }
    .custom-search .form-group {
      max-width: 50%;
      margin-bottom: 15px;
      padding-left: 15px;
      padding-right: 15px;
    }
    .custom-search .btn {
      margin: 0 15px 0 auto;
    }
    .custom-search .form-group-item {
      flex-wrap: wrap;
    }
  }
  @media (max-width: 475px) {
    .custom-search .form-group {
      max-width: 100%;
    }
  }
</style>